<template>
    <Table :columns="table.cols" :data="table.data"></Table>
</template>
<script>
import TableDataView from '@/mixins/table-data-view';
import TableColRender from '@/mixins/table-col-render';
import bus from '@/assets/eventBus';
// import ModelView from '@/mixins/model-view';
export default {
    name: 'RefundRuleList',
    mixins: [TableDataView, TableColRender],
    data () {
        return {
            table: {
                url: '/api/refund/rules/list',
                cols: [
                    {
                        title: '名称',
                        key: 'name'
                    },
                    {
                        title: '产品类型',
                        key: 'productType',
                        render: (h, params) => (
                            <span>
                                {params.row.productType === 5001
                                    ? '景点'
                                    : params.row.productType === 5002
                                        ? '酒店'
                                        : '线路'}
                            </span>
                        )
                    },
                    {
                        title: '是否可退',
                        key: 'refund',
                        render: (h, params) => (
                            <span> {params.row.refund === true ? '是' : '否'} </span>
                        )
                    },
                    {
                        title: '是否无损退',
                        key: 'free',
                        render: (h, params) => (
                            <span> {params.row.free === 5001 ? '是' : '否'} </span>
                        )
                    },
                    {
                        title: '创建时间',
                        key: 'createTime',
                        render: this.renderDate
                    },
                    {
                        title: '操作',
                        render: (h, params) =>
                            this.renderActions(h, params, [
                                {
                                    config: {
                                        loading: this.loadingBtn,
                                        style: { marginRight: '5px' }
                                    },
                                    click: () => this.update(params.row),
                                    text: '修改'
                                }
                            ])
                    }
                ],
                data: []
            }
        };
    },
    mounted () {
        bus.$on('RefundRule-update', this.doQuery);
    },
    methods: {
        update (row) {
            this.$router.push({
                name: 'RefundRuleUpdate',
                params: {
                    id: row.id,
                    productType: row.productType,
                    name: row.name,
                    refund: row.refund === true ? 1 : 0,
                    free: row.free === true ? 1 : 0,
                    rule: row.rule
                }
            });
        }
    }
};
</script>
